.list-overflow-container {
  max-height: 545px;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-top: 10px;
  /* border: 1px solid #F3969A; */
}

.list-overflow-container ul {
  margin-bottom: 20px;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}
